<template>
  <div class="list">
    <div>
      <v-toolbar flat color="gray">
        <v-toolbar-title></v-toolbar-title>
        <v-divider
         class="mx-3"
         inset
         vertical
        ></v-divider>

        <!-- 
        <v-icon @click="refresh()"></v-icon> -->

        <v-spacer></v-spacer>

        <!--   -->
        <v-btn fab dark small color="dark" class="mb-2"
          @click="dialog=true"
        >
          <v-icon dark></v-icon>
        </v-btn>
      </v-toolbar>

      <!-- 
        rows-per-page-text="" 1
        :rows-per-page-items="[]" 1
       -->
      <v-data-table
         :headers="headers"
         :items="letters"
         rows-per-page-text=""
         :rows-per-page-items="[]"
         class="elevation-1"
         v-show="!deleteDialog"
      >

        <!--  -->
        <template slot="items" slot-scope="props">
          <td class="text-xs-center">{{ props.item.id }}</td>
          <td class="text-xs-center">{{ props.item.title }}</td>
          <td class="text-xs-center">{{ props.item.body }}</td>
          <td class="text-xs-center">{{ props.item.created_date }}</td>
          <td class="text-xs-center">{{ props.item.updated_date }}</td>
          <td class="justify-center layout px-0">
            <!--  -->
            <v-icon class="mr-2" @click="editLetter(props.item.id)">
              edit
            </v-icon>
            <!--  -->
            <v-icon @click="openDeleteDialog(props.item.id)">
              delete
            </v-icon>
          </td>
        </template>

        <!--  -->
        <template slot="no-data">
          
        </template>

        <!--  -->
        <!-- <template slot="pageText" slot-scope="props">
          {{ props.itemsLength }}  {{ props.pageStart }}   {{ props.pageStop }} 
        </template> -->
      </v-data-table>

      <!--  -->
      <v-layout row justify-center>
        <v-dialog v-model="dialog" lazy persistent max-width="600px">
          <v-form  v-model="valid" lazy-validation>
          <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>
            <v-card-text>
              <v-container grid-list-md>
                <v-layout wrap>
                  <v-flex xs12 sm6 md4>
                    <v-text-field
                      v-model="selectedLetter.title"
                      label=""
                      :counter="16"
                      required
                    ></v-text-field>
                  </v-flex>
                  <v-flex xs12 sm6>
                    <v-text-field
                      v-model="selectedLetter.body"
                      label=""
                    ></v-text-field>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" flat @click="save" :disabled="!valid"></v-btn>
              <v-btn color="blue darken-1" flat @click="close"></v-btn>
            </v-card-actions>
          </v-card>
          </v-form>
        </v-dialog>
      </v-layout>

      <!--  -->
      <v-layout row justify-center>
        <v-dialog
          v-model="deleteDialog"
          persistent
          max-width="290px"
        >
          <v-card-text>
          
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" flat @click="deleteLetter"></v-btn>
            <v-btn color="blue darken-1" flat @click="close"></v-btn>
          </v-card-actions>
        </v-dialog>
      </v-layout>

    </div>
 </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'list',
  data () {
    return {
      // 
      letters: [],
      // 
      headers: [
         { text: 'ID', align: 'center', value: 'id' },
         { text: '', align: 'center', value: 'title' },
         { text: '', align: 'center', value: 'body' },
         { text: '', align: 'center', value: 'created_date' },
         { text: '', align: 'center', value: 'updated_date' }
      ],
      // 
      selectedLetter: {
        id: -1,
        title: '',
        body: '',
        created_date: '',
        updated_date: '',
      },
      // 
      defaultValue: {
        id: -1,
        title: '',
        body: '',
        created_date: '',
        updated_date: '',
      },
      dialog: false, // 
      deleteDialog: false, // 
      valid: true, // 
    }
  },
  methods: {
    // 
    indexLetters () {
      this.letters = [];
      axios.get("http://cocoahearts.xsrv.jp/api/letters")
      .then( response => {
        this.letters = response.data;
        console.log("Index : record num=" + this.letters.length);
      });
    },
    // 
    createLetter (letterInfo) {
      axios.post("http://cocoahearts.xsrv.jp/letters", letterInfo)
      .then( response => {
        console.log("Created : id=" + response.data.id);
      })
      .then( () => {
        this.indexLetters();
      });
    },
    // ()
    editUser (id) {
      this.selectedLetter.id = id
      axios.get("http://cocoahearts.xsrv.jp/api/letter/" + id)
      .then( response => {
        this.selectedLetter = response.data;
        console.log("Edit : id=" + this.selectedLetter.id);
        this.dialog = true;
      })
    },
    // ()
    updateLetter (letterInfo) {
      axios.patch("http://cocoahearts.xsrv.jp/api/letter/" + letterInfo.id, letterInfo)
      .then( response => {
        console.log("Updated : id=" + response.data.id);
      })
      .then( () => {
        this.indexLetters();
      });
    },
    // 
    openDeleteDialog (id) {
      this.selectedLetter.id = id;
      this.deleteDialog = true;
    },
    // 
    deleteLetter () {
      axios.delete("http://cocoahearts.xsrv.jp/api/letter/" + this.selectedLetter.id)
      .then( () => {
        this.indexLetters();
        this.close();
      });
    },
    // 
    // refresh (){
    //   console.log("Refresh");
    //   this.indexLetters();
    // },
    // 
    save () {
      if (this.selectedLetter.id == -1) {
        // 
        this.createLetter(this.selectedLetter);
      } else {
        // 
        this.updateLetter(this.selectedLetter);
      }
      this.close()
    },
    // 
    close () {
      this.dialog = false
      this.deleteDialog = false
      setTimeout(() => {
        this.selectedLetter = Object.assign({}, this.defaultValue)
      }, 500)
    },
  },
  computed: {
    // 
    formTitle () {
      return this.selectedLetter.id === -1 ? '' : ''
    }
  },
  mounted() {
    this.indexLetters();
  }
}
</script>

